import { writable } from 'svelte/store';
const VQUALITY = 0x00002000;
const IO_SPACE = 0x00004000;
const MUTABLE = 0x00008000;
const SIGNED = 0x00001000;
const INTERNAL = 0x00010000;
const SIZE_8 = 0x00000001;
const SIZE_16 = 0x00000002;
const SIZE_32 = 0x00000004;
const FLOAT_VALUE = 0x00000010;
const BIT_VALUE = 0x00000020;
const INT_VALUE = 0x00000040;
const BIT_MASK = 0x00000F00;
const SIZE_MASK = 0x0000000F;
const TYPE_MASK = BIT_VALUE | INT_VALUE | FLOAT_VALUE | SIGNED | SIZE_8 | SIZE_16 | SIZE_32;
var VT;
(function (VT) {
    VT[VT["VT_UNDEFINE"] = 0] = "VT_UNDEFINE";
    VT[VT["VT_DISCRETE"] = (SIZE_8 | BIT_VALUE)] = "VT_DISCRETE";
    VT[VT["VT_BYTE"] = (SIZE_8 | INT_VALUE)] = "VT_BYTE";
    VT[VT["VT_SIGNED_BYTE"] = (SIGNED | SIZE_8 | INT_VALUE)] = "VT_SIGNED_BYTE";
    VT[VT["VT_WORD"] = (SIZE_16 | INT_VALUE)] = "VT_WORD";
    VT[VT["VT_SIGNED_WORD"] = (SIGNED | SIZE_16 | INT_VALUE)] = "VT_SIGNED_WORD";
    VT[VT["VT_DWORD"] = (SIZE_32 | INT_VALUE)] = "VT_DWORD";
    VT[VT["VT_SIGNED_DWORD"] = (SIGNED | SIZE_32 | INT_VALUE)] = "VT_SIGNED_DWORD";
    VT[VT["VT_FLOAT"] = (SIGNED | SIZE_32 | FLOAT_VALUE)] = "VT_FLOAT";
})(VT || (VT = {}));
;
function is_vt_has_quality(type) {
    return type & VQUALITY ? true : false;
}
;
export default class OnlineValue {
    type_;
    value;
    check_quality_8(data) {
        if (is_vt_has_quality(this.type_) && data.get_val(1) != 0x01)
            return false;
        return true;
    }
    check_quality_16(data) {
        if (is_vt_has_quality(this.type_) && data.get_val(2) != 0x01)
            return false;
        return true;
    }
    check_quality_32(data) {
        if (is_vt_has_quality(this.type_) && data.get_val(4) != 0x01)
            return false;
        return true;
    }
    set_value(val) {
        switch (val.s) {
            case 2 /* VST_UNKNOWN */:
                this.value.set({ value: null, status: val.s, quality: 0 /* VQ_BAD */, str: "unk" });
                return;
            case 1 /* VST_DEVICE_NOT_RESPONDED */:
                this.value.set({ value: null, status: val.s, quality: 0 /* VQ_BAD */, str: "nc" });
                return;
            case 0 /* VST_DEVICE_RESPONDED */:
                let value, quality = 1 /* VQ_GOOD */, str;
                let data = val.data_vd();
                const clear_type = this.type_ & TYPE_MASK;
                switch (clear_type) {
                    case VT.VT_DISCRETE:
                        if (!this.check_quality_8(data)) {
                            str = "x";
                            quality = 0 /* VQ_BAD */;
                            break;
                        }
                        value = data.data_view.getUint8(0);
                        str = (data.data_view.getUint8(0) === 255 /* True */) ? "1" : "0";
                        break;
                    case VT.VT_BYTE:
                    case VT.VT_SIGNED_BYTE:
                        if (!this.check_quality_8(data)) {
                            str = "x";
                            quality = 0 /* VQ_BAD */;
                            break;
                        }
                        if (clear_type == VT.VT_BYTE) {
                            value = data.data_view.getUint8(0);
                            str = value.toString();
                        }
                        else {
                            value = data.data_view.getInt8(0);
                            str = value.toString();
                        }
                        break;
                    case VT.VT_WORD:
                    case VT.VT_SIGNED_WORD:
                        if (!this.check_quality_16(data)) {
                            str = "x";
                            quality = 0 /* VQ_BAD */;
                            break;
                        }
                        if (clear_type == VT.VT_WORD) {
                            value = data.data_view.getUint16(0, true);
                            str = value.toString();
                        }
                        else {
                            value = data.data_view.getInt16(0, true);
                            str = value.toString();
                        }
                        break;
                    case VT.VT_DWORD:
                    case VT.VT_SIGNED_DWORD:
                    case VT.VT_FLOAT:
                        if (!this.check_quality_32(data)) {
                            str = "x";
                            quality = 0 /* VQ_BAD */;
                            break;
                        }
                        switch (clear_type) {
                            case VT.VT_DWORD:
                                value = data.data_view.getUint32(0, true);
                                str = value.toString();
                                break;
                            case VT.VT_SIGNED_DWORD:
                                value = data.data_view.getInt32(0, true);
                                str = value.toString();
                                break;
                            case VT.VT_FLOAT:
                                value = data.data_view.getFloat32(0, true);
                                str = value.toFixed(3);
                                break;
                        }
                        break;
                    default:
                        console.error("Unknown type %d", this.type_ & TYPE_MASK);
                        return;
                }
                this.value.set({ value: value, status: val.s, quality: quality, str: str });
                break;
            default:
                console.error();
                return;
        }
    }
    constructor(type) {
        this.type_ = type;
        this.value = writable({ value: 0, status: 2 /* VST_UNKNOWN */, quality: 0, str: "unk" });
    }
}
